<template>
  <section class="center-all-videos mt-4">
    <div class="container mx-auto p-1">
      <div class="w-full h-auto">

        <div v-for="value in list" :key="value.id" class="flex flex-row align-center justify-between flex-wrap ">
          <div class="flex flex-row align-center justify-between w-full " style="height:100px">
            <div class="w-6/12 lg:w-6/12 lg:h-full md:w-5/12 md:h-24 h-full">
              <v-lazy-image @error="notShow" :src="`${value.relatives[nums].news_image[0].url}`" alt="" class="w-full h-full lg:w-full lg:h-full" style="height: 85%"/>
            </div>
            <div class="w-6/12 lg:w-6/12 md:w-5/12 md:h-24">
              <p id="message" class="text-black font-bold text-right right-0 pr-1 " style="font-size: 12px">{{value.relatives[nums].title}}</p>
              <div class="flex flex-row align-center justify-around mt-2.5 w-full">
                <div><p class="text-slate-700 font-semibold" style="font-size: 10px">9 ساعت قبل</p></div>
                <div class="flex flex-row">
                  <font-awesome-icon :icon="['fas','comment']" class="text-slate-600" style="font-size: 10px"></font-awesome-icon>
                  <span class="text-slate-600 font-semibold" style="font-size: 10px">{{value.relatives[nums].news_comment_count}}</span>
                  <font-awesome-icon :icon="['fas','eye']" class="text-slate-600" style="font-size: 10px"></font-awesome-icon>
                  <span class="text-slate-600 font-semibold" style="font-size: 10px">{{value.relatives[nums].visit}}</span>
                </div>
              </div>
            </div>
          </div>

        </div>

      </div>
    </div>
  </section>
</template>

<script>
import{FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import VLazyImage from "v-lazy-image";
import{ref} from 'vue'
export default {
  name: "TwoVideo",
  components:{FontAwesomeIcon,VLazyImage},
  props:{
    list:{
      type:Array,
      required:true,
      default:null
    },
    nums:{
      type:Number,
      required: true,
      default:null
    }
  },
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
 notFound,notShow
    }
  }
}
</script>

